import { serializeStyles } from '@emotion/serialize';
import {
  BuildMasonryStylesProps,
  BuilderReturnType,
  GapUnits,
} from '../../types';
import { cssStringToBuilderReturnType } from '../commonUtils';

export const buildMasonryStyles = ({
  selectors: { itemSelector, parentSelector },
  masonryDensity = 10,
  horizontalGap = 0,
  horizontalGapUnit = GapUnits.px,
  verticalGap = 0,
  verticalGapUnit = GapUnits.px,
}: BuildMasonryStylesProps): BuilderReturnType => {
  const parentCss = serializeStyles([{
    [parentSelector]: {
      display: 'flex',
      flexWrap: 'wrap',
      columnGap: horizontalGap + horizontalGapUnit,
      rowGap: verticalGap + verticalGapUnit,
    },
  }]).styles;

  const itemCss = serializeStyles([{
    [`${itemSelector}:nth-child(n)`]: {
      height: masonryDensity * 2 + 'vh',
      flex: '1 1 auto',
    },
  }]).styles;

  return cssStringToBuilderReturnType(parentCss + itemCss);
};
